.newsletter-area {
    padding: 100px 0;
    background-image: url(https://themehoster.com/tf/html/tm/buten/demo/assets/images/newsletter-bg.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: relative;
    .newsletter-overlay {
        background-color: rgba(34, 115, 248, 0.8);
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }
    .newsletter-box {
        padding: 40px;
        border-radius: 10px;
        background-color: #fff;
        position: relative;
        h2 {
            font-weight: 800;
            margin: 0 0 5px;
            font-size: 30px;
            line-height: 40px;
        }
        p {
            text-align: left;
        }
        .newsletter-form {
            position: relative;
            margin: 14px 0 0;
            input{
                border-radius: 30px;
                height: 50px;
                -webkit-box-shadow: 0px 0px 15px 0px rgb(52 69 199 / 40%);
                box-shadow: 0px 0px 15px 0px rgb(52 69 199 / 40%);
                font-size: 14px;
                overflow: visible;
                display: block;
                width: 100%;
                padding: 0.375rem 0.75rem;
                line-height: 1.5;
                color: #495057;
                background-color: #fff;
                background-clip: padding-box;
                border: 1px solid #ced4da;
                transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
                &::placeholder {
                    padding-left: 30px;
                }
            }
            button {
                position: absolute;
                right: 0;
                top: 0;
                height: 50px;
                padding: 0 40px;
                border-radius: 30px;
                border: 0;
                background-color: #2273f8;
                color: #fff;
                outline: 0;
                cursor: pointer;
                transition: all 0.6s ease;
                &:hover {
                    background-color: #1b5ac0;
                    color: #fff;
                }
            }
        }
    }
}




@media (max-width: 768px) {
    .newsletter-area {
        padding: 80px 0px !important;
        .newsletter-box {
            margin: 0px 15px;
            padding: 40px 15px 50px !important;
            text-align: center;
            .col-md {
                flex: 0 0 100% !important;
                max-width: 100% !important;
            }
            .col-lg {
                flex: 0 0 70% !important;
                max-width: 70% !important;
                margin: 0 auto;
            }
            h2 {
                font-size: 30px;
                line-height: 36px;
            }
            p {
                text-align: center !important;
            }
            .newsletter-form {
                
                margin-top: 30px;
                form {
                    display: flex;
                    flex-direction: column;
                    input {
                        height: 50px !important;
                    }
                    button {
                        height: 50px;
                    }
                }
            }
        }
    }
}


@media (max-width: 425px) {
    .newsletter-area {
        padding: 80px 0px !important;
        .newsletter-box {
            margin: 0px 15px;
            padding: 40px 15px 50px !important;
            text-align: center;
            h2 {
                font-size: 26px;
                line-height: 36px;
            }
            p {
                text-align: center !important;
            }
            .newsletter-form {
                form {
                    display: flex;
                    flex-direction: column;
                    input {
                        height: 45px !important;
                    }
                    button {
                        position: unset;
                        height: 45px;
                        width: 100%;
                        margin-top: 10px;
                    }
                }
            }
        }
    }
}